@import url(https://fonts.googleapis.com/css?family=Codystar|Roboto);
.drum-machine {
  width: 500px;
  height: 800px;
  background-color: black;
  border: 15px solid #333333;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 30px 20px -15px rgba(0, 0, 0, 0.7); }

.drum-machine__pad-container {
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px 20px; }

.display {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 390px;
  height: 230px;
  margin-top: 35px;
  background-color: #6a9afb;
  font-family: "Codystar", cursive;
  font-size: 50px;
  font-weight: bolder;
  color: #264eed; }

.drum-pad {
  width: 130px;
  height: 85px;
  background-color: #333333;
  border: 3px solid #e0e0e0;
  border-radius: 3px;
  cursor: pointer;
  color: white;
  text-align: right;
  padding: 10px;
  font-family: "Roboto", sans-serif;
  font-weight: bolder; }

.active {
  border-color: #7890fc; }

* {
  box-sizing: border-box; }

body {
  background-color: #2e88ef; }

#root {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center; }

