.drum-machine {
  width: 500px;
  height: 800px;
  background-color: black;
  border: 15px solid $dark-grey;
  border-radius: 15px;
  display: flex;
  flex-direction: column;
  align-items: center;
  box-shadow: 0 30px 20px -15px rgba(0, 0, 0, 0.7); }

.drum-machine__pad-container {
  margin-top: 60px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 30px 20px; }
